import User from '../../img/Male User.png';
import '../../css/RegisterAndLogin.css';
import { Link, useNavigate } from 'react-router-dom';
import axios from '../axios';
import { useEffect, useState } from 'react';
export default function Register() {
  const [fullName, setFullName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errors, setErrors] = useState('');
  const [token, setToken] = useState('');

  const navigate = useNavigate();
  useEffect(() => {
    if (window.localStorage.getItem('token')) {
      navigate('/');
    }
  }, []);
  const register = () => {
    axios
      .post('/auth/register', {
        fullName: fullName,
        email: email,
        password: password,
      })
      .then((res) => {
        window.localStorage.setItem('token', res.data.token);
        window.localStorage.setItem('Auth', true);
        navigate('/');
      })
      .catch((error) => {
        if (error.response && error.response.data.errors) {
          const backendErrors = {};
          error.response.data.errors.forEach((err) => {
            backendErrors[err.path] = err.msg;
          });
          setErrors(backendErrors);
        } else {
          console.error('Unexpected error:', error);
        }
      });
  };
  return (
    <div className="container">
      <div className="center">
        <div className="Register">
          <p className="title-register">Register</p>
          <img src={User} alt="" className="userMale" />
          <div>
            <label
              htmlFor=""
              className={`label-validator ${
                errors.fullName === "'    3- !" ? 'error' : ''
              }`}>
              {errors.fullName === "'    3- !"
                ? "'    3- !"
                : "I'"}
            </label>
            <input
              type="text"
              value={fullName}
              onChange={(e) => setFullName(e.target.value)}
              className={`inputs-register ${
                errors.fullName === "'    3- !" ? 'error' : ''
              }`}
            />
          </div>
          <div>
            <label
              htmlFor=""
              className={`label-validator  ${errors.email === ' !' ? 'error' : ''}`}>
              {errors.email === ' !' ? ' !' : ''}
            </label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className={`inputs-register ${errors.email === ' !' ? 'error' : ''}`}
            />
          </div>
          <div>
            <label
              htmlFor=""
              className={`label-validator ${
                errors.password === '    5 !' ? 'error' : ''
              }`}>
              {errors.password === '    5 !'
                ? '    5 !'
                : ''}
            </label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className={`inputs-register ${
                errors.password === '    5 !' ? 'error' : ''
              }`}
            />
          </div>
          <p className="errors">
            {errors.password === '  ' ? '  ' : ''}
          </p>
          <p className="EAccount">
              ? <Link to="/login">  </Link>
          </p>
          <p onClick={() => register()} className="btn-submit">
            
          </p>
        </div>
      </div>
    </div>
  );
}
