import User from '../../img/Male User.png';
import '../../css/RegisterAndLogin.css';
import { Link, useNavigate } from 'react-router-dom';
import { useEffect, useState } from 'react';
import axios from '../axios';
export default function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errors, setErrors] = useState('');
  const navigate = useNavigate();
  if (window.localStorage.getItem('token')) {
    navigate('/');
  }
  const PostLogin = () => {
    axios
      .post('/auth/login', {
        email,
        password,
      })
      .then((res) => {
        window.localStorage.setItem('token', res.data.token);
        window.localStorage.setItem('Auth', true);
        navigate('/');
      })
      .catch((error) => {
        setErrors(error.response.data.message);
      });
  };
  return (
    <div className="container">
      <div className="center">
        <div className="Register">
          <p className="title-register">Login</p>
          <div>
            <label
              htmlFor=""
              className={`label-validator  ${errors.email === ' !' ? 'error' : ''}`}>
              {errors.email === ' !' ? ' !' : ''}
            </label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className={`inputs-register ${errors.email === ' !' ? 'error' : ''}`}
            />
          </div>
          <div>
            <label
              htmlFor=""
              className={`label-validator ${
                errors.password === '    5 !' ? 'error' : ''
              }`}>
              {errors.password === '    5 !'
                ? '    5 !'
                : ''}
            </label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className={`inputs-register ${
                errors === '    5 !' ? 'error' : ''
              }`}
            />
          </div>
          <p className="errors">{errors}</p>
          <p className="EAccount">
              ? <Link to="/register">  </Link>
          </p>
          <p onClick={() => PostLogin()} className="btn-submit">
            
          </p>
        </div>
      </div>
    </div>
  );
}
